import React from 'react';

import Card from '../components/Card';

import manga24 from '../assets/images/solo.jpg';
import ava from '../assets/images/ava.png';
import react from '../assets/images/react.jpg';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';

class Carousel extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            items: [
                {
                    id: 0,
                    title: 'Manga24',
                    subTitle: 'The right site for manga, manhwa addicts',
                    imgSrc: manga24,
                    link: 'http://manga24.000webhostapp.com/',
                    selected: false
                },
                {
                    id: 1,
                    title: 'Steve Nguyen',
                    subTitle: 'Linkedin Profile',
                    imgSrc: ava,
                    link: 'https://linkedin.com/in/hoangnguyendinh1107',
                    selected: false
                },
                {
                    id: 2,
                    title: 'Wonderful Resort',
                    subTitle: 'A Hotel Information Site built with React',
                    imgSrc: react,
                    link: 'https://nah-resortv2.netlify.app',
                    selected: false
                },
            ]
        }
    }


    handleCardClick = (id, card) => {

        let items = [...this.state.items];

        items[id].selected = items[id].selected ? false : true;

        items.forEach(item => {
            if(item.id !== id) {
                item.selected = false;
            }
        });

        this.setState({
            items
        });
    }


    makeItems = (items) => {
        return items.map(item => {
            return <Card item={item} click={(e => this.handleCardClick(item.id, e))} key={item.id} />
        })
    }


    render() {
        return(
            <Container fluid={true}>
                <Row className="justify-content-around">
                    {this.makeItems(this.state.items)}
                </Row>
            </Container>
        );
    }

}

export default Carousel;