import React from 'react';
import Hero from '../components/Hero';
import Content from '../components/Content';

function AboutPage(props) {

    return(
        <div>
            <Hero title={props.title} />

            <Content>
            <p>Hello, my name is Steve, or you can address me as Hoang. I'm an electrical and electronic engineering student who is passonate about algorithms and programming.</p>

            <p>I'm constantly learning new things, currently those things include gaining more experience with JavaScript, React, PHP and NodeJS</p>

            <p>My latest project, Wonderful Resort, is a hotel information site. You can check it out <a href="https://nah-resortv2.netlify.com" target="_blank" rel="noopener noreferrer">here</a>, or on the homepage. It is built with React, uses Contentful for data management, and Netlify to deploy. However, I will be expand it using NodeJS and RestAPI in the coming months</p>

            <p>Since I'm still a student, I need to manage and balance the time for the coding stuff, academic study, and my volunteer experience at AIESEC, a wordwide youth organization. You can find me through <a href="https://www.linkedin.com/in/hoangnguyendinh1107" target="_blank" rel="noopener noreferrer">here</a></p>
            </Content>
        </div>
    );

}

export default AboutPage;