import React from 'react'
import Container from 'react-bootstrap/Container';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import Col from 'react-bootstrap/Col'
import { Row } from 'react-bootstrap';

export default function Footer() {
    return (
        <footer className="border-top">
            <Container fluid={true} className="mt-5 justify-content-center" >
                <Row>      
                    <Col md={5} sm={12}>
                        <h5>Steve Nguyen</h5>
                        <p className="bio">An electrical and electronic engineering student with passion about programming and computer stuff</p>
                    </Col>
                    <Col md={5} sm={12}>
                        <Row>
                            <span className="icon"><FontAwesomeIcon icon="map-marker" /></span>
                            <p><span>Lien Ying Chow Drive</span>, Singapore</p>
                        </Row>
                        <Row>
                            <span className="icon"><FontAwesomeIcon icon="phone"/></span> 
                            <p>8586 4103</p>
                        </Row>
                        <Row>
                            <span className="icon"><FontAwesomeIcon icon="envelope" /></span>
                            <p><a href="mailto:hoangnguyendinh1107@gmail.com">hoangnguyendinh1107@gmail.com</a></p>
                        </Row>
                    </Col>
                    <Col md={2} sm={12}>
                        <h5>Social Network</h5>
                        <a className="social" href="https://www.facebook.com"><FontAwesomeIcon icon={['fab', 'facebook']} size="lg"/></a>
                        <a className="social" href="https://www.twitter.com"><FontAwesomeIcon icon={['fab', 'twitter']} size="lg" /></a>
                        <a className="social" href="https://www.linkedin.com/in/hoangnguyendinh1107/"><FontAwesomeIcon icon={['fab', 'linkedin']} size="lg" /></a>
                        <a className="social" href="https:/github.com/NahSama"><FontAwesomeIcon icon={['fab', 'github']} size="lg" /></a> 
                    </Col>
                </Row>
            </Container>
        </footer>
    )
}
