import React from 'react';
import {BrowserRouter as Router, Route, Link, Switch } from 'react-router-dom';
import Container from 'react-bootstrap/Container';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import './App.css';

import Footer from './components/Footer';
import HomePage from './pages/HomePage';
import AboutPage from './pages/AboutPage';
import Error from './pages/Error';
import ContactPage from './pages/ContactPage';

import { library } from '@fortawesome/fontawesome-svg-core'
import { fab } from '@fortawesome/free-brands-svg-icons'
import { faPhone, faMapMarker, faEnvelope } from '@fortawesome/free-solid-svg-icons'

library.add(fab, faPhone, faEnvelope, faMapMarker)

class App extends React.Component {
  
  constructor(props) {
    super(props);
    this.state = {
      title: 'Garrett Love',
      headerLinks: [
        { title: 'Home', path: '/' },
        { title: 'About', path: '/about' },
        { title: 'Contact', path: '/contact' }
      ],
      home: {
        title: 'Be Consistent',
        subTitle: 'Lack of consistency brings on a lack of interest',
        text: 'Checkout my projects below'
      },
      about: {
        title: 'About Me'
      },
      error: {
        title: 'ERROR 404'
      }
    }
  }

  render() {
    return (
      <Router>
        <Container className="p-0" fluid={true}>
          
          <Navbar className="border-bottom" bg="transparent" expand="lg">
            <Navbar.Brand>Steve Nguyen</Navbar.Brand>

            <Navbar.Toggle className="border-0" aria-controls="navbar-toggle" />
            <Navbar.Collapse id="navbar-toggle">
              <Nav className="ml-auto">
                <Link className="nav-link" to="/">Home</Link>
                <Link className="nav-link" to="/about">About</Link>
              </Nav>
            </Navbar.Collapse>
          </Navbar>
            <Switch>
                <Route path="/" exact render={() => <HomePage title={this.state.home.title} subTitle={this.state.home.subTitle} text={this.state.home.text} />} />
                <Route path="/about" render={() => <AboutPage title={this.state.about.title} />} />
                {/* <Route path="/contact" render={() => <ContactPage title={this.state.contact.title} />} /> */}
                <Route render={() => <Error title={this.state.error.title}/>}/>
            </Switch>
            <Footer />

        </Container>
      </Router>
    );
  }
}

export default App;
